/* Global Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'IBM Plex Mono Nerd Font', monospace;
  color: #fff;
  background: linear-gradient(to bottom right, #000051, #d40029); /* Gradient background */
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow-x: hidden;
}

.App {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  text-align: center;
}

/* Header */
.header {
  text-align: center;
  margin-bottom: 40px;
}

.logo {
  width: 150px;
  height: auto;
  margin-bottom: 20px;
}

.title {
  font-size: 2rem;
  color: #fff;
  margin-bottom: 20px;
}

/* Content */
.content {
  display: flex;
  flex-direction: column;
  gap: 40px;
}

/* Forms */
form {
  background: rgba(255, 255, 255, 0.1);
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(5px);
  width: 100%;
  max-width: 600px;
}

label {
  display: block;
  margin-bottom: 10px;
  font-weight: bold;
}

input,
textarea {
  width: 100%;
  padding: 10px;
  border: none;
  border-radius: 5px;
  background: rgba(255, 255, 255, 0.2);
  color: #fff;
  font-size: 1rem;
  outline: none;
  transition: all 0.3s ease;
}

input:focus,
textarea:focus {
  background: rgba(255, 255, 255, 0.3);
}

button {
  padding: 12px 24px;
  font-size: 1rem;
  font-weight: bold;
  border: none;
  border-radius: 6px;
  background: #fff;
  color: #000;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background: #f0f0f0;
}

/* Error Message */
.error {
  color: red;
  margin-top: 10px;
}

/* Shipping Label */
.shipping-label {
  background: rgba(255, 255, 255, 0.1);
  padding: 40px;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(5px);
  width: 100%;
  max-width: 600px;
  white-space: pre-wrap;
  line-height: 1.8;
}

.shipping-label h2 {
  font-size: 1.5rem;
  margin-bottom: 20px;
}

.shipping-label h3 {
  font-size: 1.2rem;
  margin-bottom: 10px;
}

.shipping-label p {
  margin-bottom: 5px;
}

@media (max-width: 600px) {
  .logo {
    width: 100px;
  }

  .title {
    font-size: 1.5rem;
  }
}

/* Print Styles */
/* --- PRINT STYLES --- */
@media print {
  /* Hide everything except the shipping label */
  body * {
    visibility: hidden;
    color: #000 !important;
    background-color: #fff !important;
    font-family: monospace;
  }

  .shipping-label,
  .shipping-label * {
    visibility: visible;
  }

  /* Ensure label prints on one page */
  .shipping-label {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    max-width: 100%;
    padding: 40px 20px;
    margin: 0;
    box-shadow: none;
    border: none;
    background-color: #fff !important;
    color: #000 !important;
    page-break-after: avoid;
    break-inside: avoid;
    -webkit-column-break-inside: avoid;
  }

  /* Force all text to be black for clarity */
  .shipping-label p,
  .shipping-label h2,
  .shipping-label h3 {
    color: #000 !important;
  }

  /* Hide button and other UI elements during print */
  .no-print,
  .no-print * {
    display: none !important;
  }

  /* Remove unwanted browser-added headers/footers */
  @page {
    size: A4 portrait;
    margin: 0; /* Removes default margins that cause extra page */

    /* Remove browser-generated headers/footers */
    @top-center {
      content: none;
    }
    @bottom-center {
      content: none;
    }
  }
}